import {useState, useEffect, useRef} from 'react'
import {Formik, Form, Field, ErrorMessage} from 'formik' 
import * as Yup from 'yup'
import api from '../../../helpers/api'
import { toast } from 'react-toastify';


const FILE_SIZE = 1 * 1024 * 1024;
const SUPPORTED_FORMATS = [
    "image/jpg",
    "image/jpeg",
    "image/png"
];

const CreateProducts = ({history}) =>{
    const [categories, setCategories] = useState([])
    const previewRef = useRef('preview')
    
    useEffect(()=>{
        api.get('/category')
            .then(res => setCategories(res.data.categories))
    }, [])

    return (
        <div className="CreateProducts">
            <div className="form-container">
                <Formik
                    initialValues={{ 
                        title: '', 
                        price: 0,
                        category: '',
                        description: '',
                        productImage: '',
                        location: '',
                        stock: 0,
                    }}
                    onSubmit={values => {
                        let data = new FormData()
                        data.append("title", values.title)
                        data.append("productImage", values.productImage)
                        data.append("description", values.description)
                        data.append("category", values.category)
                        data.append("price", values.price)
                        data.append("stock", values.stock)
                        data.append("location", values.location)
                        api.post('/products', data,{
                            headers:{
                                "Content-Type": "multipart/form-data"
                            }
                        })
                            .then(res => {
                                history.push(`/admin/products/${res.data.productId}`)
                                toast.success("Product Added Successfully")
                            })
                            .catch(e => console.log(e))
                    }}
                    validationSchema={Yup.object({

                        title: Yup.string().required('You must provide a Name'), 
                        price: Yup.number().required('You must provide a Price'),
                        stock: Yup.number().required('You must provide the item quantity'),
                        category: Yup.string().required('Pick a category'),
                        location: Yup.string().required('you must provide a location'),
                        description: Yup.string().required('Provide a Description'), 
                        productImage: Yup.mixed().required("A file is required")
                            .test("fileSize", "File too large",
                                (value) => value && value.size <= FILE_SIZE
                            )
                            .test("fileFormat", "Unsupported Format",
                                (value) => value && SUPPORTED_FORMATS.includes(value.type)
                            ),
                    })}
                >
                    {props => (<Form>
                        <div className="content">
                            <p className={'form-error'}><ErrorMessage name='title'/></p>
                            <div className="form-input">
                                <label htmlFor="title" title='Product Name'></label>
                                <Field type="text" name='title' placeholder='Product Name' />
                            </div>

                            <p className={'form-error'}><ErrorMessage name='category'/></p>
                            <div className="form-input">
                                <label htmlFor="category" title='Category'></label>
                                <Field as="select" name='category' placeholder='Category' title='Category'>
                                    <option value="" disabled>Select a Category</option>
                                    {categories.map((item) => <option key={item.value} value={item.value}>{item.label}</option>)}
                                </Field>
                            </div>

                            <p className={'form-error'}><ErrorMessage name='price'/></p>
                            <div className="form-input">
                                <label htmlFor="price" title="price"></label>
                                <span className="input-addon"><p>₦</p></span>
                                <Field type="number" name='price' placeholder='Price' min='0'/>
                            </div>
                            
                            <p className={'form-error'}><ErrorMessage name='location'/></p>
                            <div className="form-input">
                                <label htmlFor="location" title="location"></label>
                                <Field type="text" name='location' placeholder='Location' />
                            </div>
                            
                            <p className={'form-error'}><ErrorMessage name='stock'/></p>
                            <div className="form-input">
                                <label htmlFor="stock" title="stock"></label>
                                <Field type="number" name='stock' placeholder='Stock' min='0'/>
                            </div>

                            <p className={'form-error'}><ErrorMessage name='description'/></p>
                            <div className="form-input">
                                <label htmlFor="description" title="Description"></label>
                                <Field as="textarea" name='description' placeholder='Description' />
                            </div>

                            <button type="submit"> Create </button>
                        </div>
                        <div className="content"style={{backgroundColor: '', height: '110px'}}>
                            <p className={'form-error'}><ErrorMessage name='productImage'/></p>
                            <div className="form-input upload">
                                <label htmlFor="productImage" title='Product Image'></label>
                                <input type="file" name='productImage' placeholder='Image Upload' onChange={(e) => {
                                    props.setFieldValue("productImage", e.target.files[0])
                                    if(e.target.files.length > 0){
                                        previewRef.current.src = URL.createObjectURL(e.target.files[0])
                                    }
                                    else{
                                        previewRef.current.src = '/static/productPlaceholder.png'
                                    }
                            }}/>
                        <img id="preview" alt="Product" ref={previewRef} src='/static/productPlaceholder.png' />
                        
                            </div>
                        </div>
                    </Form>)}
                </Formik>
            </div>
        </div>  
    )
}

export default CreateProducts
